h1,
h2 {
  margin: 0;
  padding: 0; }

.container {
  background-color: #333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #EEE;
  height: 100vh; }

.divider {
  background-color: #bc17e6;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  margin-top: 30px;
  margin-bottom: 40px;
  width: 25%; }

.profileImage {
  z-index: 10;
  margin-bottom: 10px; }
  .profileImage img {
    width: 230px;
    border-radius: 50%;
    border: 1px solid #bc17e6;
    box-shadow: 3px 2px 3px #bc17e6; }

.name {
  z-index: 10;
  font-size: 1.25em; }

.about {
  z-index: 10;
  margin-top: 0;
  font-size: 0.7em;
  margin-bottom: 30px;
  color: #888; }

.socialSites {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 20px; }

.projects {
  background-color: chartreuse; }
