.terminalContainer {
  background-color: #222;
  border-radius: 5px;
  padding: 0px 5px;
  padding-bottom: 3px;
  color: #EEE;
  font-size: 15px; }

.before {
  margin-right: 5px;
  color: #bc17e6; }

.cursor {
  margin-left: 5px;
  color: #bc17e6; }
  .cursor.show {
    opacity: 1; }
  .cursor.hide {
    opacity: 0; }
